<template>
  <div class="main-container col2-right-layout">
    <div class="main container">
      <div class="row">
        <section>
          <div class="col-main">
            <div class="dashboard">
              <div class="welcome-msg" id="helloName"></div>
              <div class="recent-orders">
                <div class="title-buttons">
                  <strong></strong><a id="oldOder">&nbsp;</a>
                </div>
                <div class="table-responsive">
                  <table class="data-table" id="my-orders-table">
                    <thead>
                      <tr class="first last">
                        <th> #</th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th><span class="nobr"></span></th>
                        <th></th>
                        <th>&nbsp;</th>
                      </tr>
                    </thead>
                    <tbody id="naka"></tbody>
                  </table>
                </div>
              </div>
              <div class="box-account">
                <div class="page-title">
                  <h1><strong> </strong></h1>
                </div>
                <div class="col2-set">
                  <div class="col-1">
                    <h5> </h5>
                    <a href="#"></a>
                    <div id="memberinformation">
                      <!-- <p>John Doe<br> jone-doe@example.com<br><a href="#">Change Password</a></p> -->
                    </div>
                  </div>
                  <div class="col-2"></div>
                </div>
                <div class="col2-set">
                  <h2><strong></strong></h2>
                  <div class="col-1">
                    <h5> </h5>
                    <div id="memberAdress">
                      <!-- <address>John Doe<br> USA<br> Anytown, Alabama, 46532<br> United States<br> T: 454541<br><a
                          href="#">Edit Address</a></address> -->
                    </div>
                  </div>
                  <div class="col-2"></div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
// import store from "@/store/index";

export default {
  name: "MyDashboard",
  mounted() {
    let Names;
    let Orders;
    var settings = {
      url: "http://localhost:8081/api/test/session/mid",
      method: "GET",
    };

    $.ajax(settings).done(function (response) {
      console.log(response);

      var settings = {
        url: `http://localhost:8081/api/member/${response}`,
        method: "GET",
        timeout: 0,
      };
      $.ajax(settings).done(function (e) {
        console.log(e);

        Names = JSON.parse(e);
        console.log("MID=" + Names.memberId);

        $("#helloName").append(
          `<strong>,<h2> ${Names.memberFirstname} ${Names.memberLastname}</h2> </strong>`
        );
        $(
          "#memberinformation"
        ).append(` <p> : ${Names.memberFirstname} ${Names.memberLastname}<br> : ${Names.memberEmail}<br></p>
`);
        $("#memberAdress").append(
          ` <address>: ${Names.memberFirstname} ${Names.memberLastname}<br> : ${Names.memberAddr}<br> : ${Names.memberTel} <br></address>`
        );

        //
        var settings = {
          url: `http://localhost:8081/api/orders/${Names.memberId}`,
          method: "GET",
        };
        $.ajax(settings).done(function (e) {
          Orders = e;
          Orders.sort(function (a, b) {
            return b.orderId - a.orderId;
          });
          $.each(Orders, (index, products) => {
            //   if(products.==n){
            $("#naka").append(` <tr >
                <td>${products.orderId}</td>
                <td>${products.createDate}</td>
                <td>${products.payMethod}</td>
                <td>${
                  products.deliverCvs != null ? products.deliverCvs : ""
                }</td>
                <td>${
                  products.deliverAddr != null ? products.deliverAddr : ""
                }</td>
                <td><span class="price">$${
                  products.orderTotalAmount
                }</span></td>
                <td><em>${products.status}</em></td>
                <td class="a-center last"><span class="nobr"></td>
              </tr>`);
          });
        });
        //
      });
    });
  },
};
</script>

